@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
}
.pred{
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  margin-top: 10px;
}
.app_header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.app_headerControls{
  display: flex;
  align-items: center;
}
.app_dark{
  background-color: #212121;
  transition: background-color 0.5s;
  color: white;
  
}
.app_darkSelect{
  background-color: #626262;
}
body{
  background-color: #f5f6fa;
}
.app_stats{
  display: flex;
  justify-content: space-between;
}
.darkModeController{
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  margin-top: 12px;
}
.app{
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}
.app_left{
  flex: 0.9 1;
}

@media (max-width:990px){
  .app{
    flex-direction: column;
  }
}

.app_header > h1{
color: #fc3c3c;
font-size: 2rem;
}

.app_select{
  background-color: white;
}
.app_right{
  display: flex;
  flex-direction: column;
}

.app_right .MuiCardContent-root{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.app_graph{
  display: flex;
  flex-grow: 1;
}
.app_graph canvas{
  width: 320px !important;
}

@media (max-width:990px){
  .app_graph canvas{
      width: 85vw !important;
  }
}
.app_graphTitle{
  margin-top: 20px;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
}
.app_graphTitle2{
  display: flex;
  justify-content: center;
}
.app_header{
  margin-bottom: 10px;
}
.infobox{
    flex: 1 1;
    
}
.infobox_cases{
    color: rgb(143, 4, 4);
    font-weight: 700;
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}
.infobox_total{
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;

}
.infobox:not(:last-child){
    margin-right: 15px;
}
.infobox:hover{
    cursor: pointer;
}
.infobox_selected{
    border-top: 10px solid greenyellow;
    transition: border-top 0.4s !important;
}
.infobox_red{
    border-color: red;
}
.infobox_green{
    color: lightgreen;
}
.infobox_dark{
    background-color: #626262 !important;
}
.map{
    height: 500px;
    background-color: white;
    border-radius: 20px;
    padding: 1rem;
    margin-top: 16px;
    
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}
.map .leaflet-container{
    height: 100%;
}
.info_confirmed,
.info_recovered,
.info_deaths{
    font-size: 16px;
    margin-top: 10px;
}
.info_flag{
    height: 100px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
    border: 1px solid black;
    margin-bottom: 5px;
}
.info_flag img{
    width: 100px;
    border-radius: 5px;
}
.info_container{
    width: 150px;
}
.leaflet-popup-content-wrapper{
    background-color: white;
}
.info_name{
    font-size: 20px;
    font-weight: bold;
    color: #555;
}
@media (max-width:990px){
    .map{
        margin-bottom: 16px;
    }
  }
.table{
    margin-top: 20px;
    overflow: scroll;
    height: 400px;
    overflow-x: hidden;
    color: #6a5d5d;
}

.table tr{
    display: flex;
    justify-content: space-between;
}
.table td{
    padding: 0.5rem;
}
.table tr:nth-of-type(odd){
    background-color: #f3f2f8;
}
